<template>
    <section class="record">
        <!--列表-->
        <el-table :data="lists" highlight-current-row v-loading="listLoading" class="lists" stripe>
            <el-table-column type="index" width="50" fixed="left"></el-table-column>
            <el-table-column prop="rc_time" label="创建时间" min-width="180"></el-table-column>
            <el-table-column prop="gate_id" label="闸口编号" min-width="120"></el-table-column>
            <el-table-column prop="chnl_no" label="通道编号" min-width="120"></el-table-column>
            <el-table-column prop="read_mode" label="读单模式" min-width="120"></el-table-column>
            <el-table-column prop="co_id" label="箱号" min-width="180"></el-table-column>
            <el-table-column prop="co_id2" label="箱号2" min-width="180"></el-table-column>
            <!--<el-table-column prop="gross_wt" label="地磅重量" min-width="120"></el-table-column>-->

            <el-table-column prop="custom_command" label="海关指令" min-width="120">
                <template scope="scope">
                    <span :class="scope.row.custom_command !== '海关放行'?'error':''">{{ scope.row.custom_command }}</span>
                </template>
            </el-table-column>
            <el-table-column prop="outgate_success" label="过闸状态" min-width="120">
                <template scope="scope">
                    <span :class="scope.row.outgate_success !== 'Y'?'error':''">{{ scope.row.outgate_success }}</span>
                </template>
            </el-table-column>

            <el-table-column prop="plc_msg" label="详情" min-width="100">
                <template scope="scope">
                    <el-popover trigger="click" placement="bottom" ref="popover">
                        <el-table :data="scope.row.msg_list" class="msg-table" stripe border max-height="400">
                            <el-table-column min-width="175" property="rc_time" label="时间"></el-table-column>
                            <el-table-column min-width="70" property="type" label="类型"></el-table-column>
                            <el-table-column min-width="200" property="msg" label="命令信息"></el-table-column>
                        </el-table>
                    </el-popover>
                    <el-button v-popover:popover size="small"><i class="el-icon-caret-bottom"></i></el-button>
                </template>
            </el-table-column>

            <!--<el-table-column prop="i_e_type" label="进出标志" min-width="100"></el-table-column>
            <el-table-column prop="co_reco" label="识别是否正常" min-width="120"></el-table-column>
            <el-table-column prop="ic_gross_wt" label="卡内重量" min-width="120"></el-table-column>
            <el-table-column prop="ic_dr_c_no" label="司机信息" min-width="120"></el-table-column>
            <el-table-column prop="seal" label="电子关锁号" min-width="120"></el-table-column>
            <el-table-column prop="ic_bill_no" label="单证号" min-width="120"></el-table-column>
            <el-table-column prop="car_ec_no2" label="电子车牌ID2" min-width="120"></el-table-column>

            <el-table-column prop="ic_ve_c_no" label="车辆海关编号" min-width="120"></el-table-column>
            <el-table-column prop="dr_ic_no" label="驾驶员IC卡号" min-width="120"></el-table-column>
            <el-table-column prop="co_num" label="箱号数量" min-width="120"></el-table-column>
            <el-table-column prop="ve_name" label="实际车辆车牌号" min-width="135"></el-table-column>
            <el-table-column prop="ic_conta_id" label="集装箱号" min-width="120"></el-table-column>
            <el-table-column prop="ic_eseal_id" label="关锁号" min-width="120"></el-table-column>
            <el-table-column prop="seq_no" label="报文序列号" min-width="180"></el-table-column>
            <el-table-column prop="car_ec_no" label="电子车牌ID1" min-width="120"></el-table-column>
            <el-table-column prop="ve_wt" label="车自重" min-width="120"></el-table-column>
            <el-table-column prop="ic_co_c_no" label="公司海关编号" min-width="120"></el-table-column>
            <el-table-column prop="ve_c_no" label="海关车辆编号" min-width="120"></el-table-column>
            <el-table-column prop="ic_ve_name" label="车牌号" min-width="120"></el-table-column>-->
        </el-table>
        <el-pagination class="pagination"
                       @current-change="handleCurrentChange"
                       :page-count="pageTotal"
                       layout="total, prev, pager, next, jumper"
                       >
        </el-pagination>
    </section>
</template>
<script>
    import { getGateRecord } from '@/api/api'

    export default {
        data () {
            return {
                token: localStorage.getItem('token'),
                listLoading: false,
                lists: [],
                pageTotal: 0
            }
        },
        methods: {
            getLists (page) {
                this.listLoading = true
                getGateRecord({
                    token: this.token,
                    page_idx: page,
                    page_cap: 5,
                    gate_id:"A"
                }).then((res) => {
                    this.lists = res.page_data
                    console.log(res)
                    this.pageTotal = res.page_total_cnts
                    this.listLoading = false
                })
            },
            handleCurrentChange (page) {
               this.getLists(page)
            }
        },
        mounted () {
            this.getLists(1)
        }
    }

</script>

<style scoped lang="scss">
    .record {
        margin-top: 10px;
        .lists {
            width: 100%;
            .msg-table{
                height: 500px;

            }
            .error {
                color: #fff;
                background-color: rgb(255, 73, 73);
                display: inline-block;
                width: 100%;
                height: 100%;
                text-align: center;
            }
        }
        .pagination {
            margin-top: 10px;
        }
    }
</style>


